import React, {Component} from 'react';
import Webcam from "react-webcam";
// import axios from 'axios';

import { MoodRequest } from "./generated/smileapp/src/main/proto/smileservice_pb";
import { SmileServiceClient } from "./generated/smileapp/src/main/proto/smileservice_grpc_web_pb";


class WebcamCapture extends Component {
    constructor(props){
        super(props);
        this.webcamRef = React.createRef();
        this.onCapture = this.onCapture.bind(this);

        this.state = {
            capturedImages: [],
            currentEmotion: '',
        };
    }

    componentDidMount() {
        setInterval(this.onCapture, 3100);
    }


    static base64ToArrayBuffer(base64) {
        let binary_string = window.atob(base64);
        let len = binary_string.length;
        let bytes = new Uint8Array(len);
        for (let i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }
        return bytes;
    }

    static evaluateEmotion(emotionHash) {
        let emotion = Object.keys(emotionHash).reduce((a, b) => emotionHash[a] > emotionHash[b] ? a : b);
        // "anger":0.272,"contempt":0.0,"disgust":0.0,"fear":0.0,"happiness":0.0,"neutral":0.728,"sadness":0.0,"surprise":0.0
        switch (emotion) {
            case 'anger':
                return 'üò†';
            case 'contempt':
                return 'üò´';
            case 'disgust':
                return 'ü§¢';
            case 'fear':
                return 'üò®';
            case 'happiness':
                return 'üòä';
            case 'sadness':
                return 'üòî';
            case 'surprise':
                return 'üò≤';
            default: // neutral
                return 'üòê';

        }
    }

    onCapture() {
        const imageSrc = this.webcamRef.current.getScreenshot();
        this.setState((prevState)=>{
            const { capturedImages } = prevState;
            capturedImages.push(imageSrc);
            return {capturedImages: capturedImages};
        });

        const imageBuffer = WebcamCapture.base64ToArrayBuffer(imageSrc.replace('data:image/jpeg;base64,', ''));

        const client = new SmileServiceClient('http://localhost:8080', null, null);

        const streamRequest = new MoodRequest();
        streamRequest.setContenttype('image/jpeg');
        streamRequest.setBody(imageBuffer);

        const stream = client.detectMood(streamRequest, {});
        stream.on('data', (response) => {
            this.setState({currentEmotion: response.getEmoji()});
        });

    }

    render(){
        const { capturedImages, currentEmotion } = this.state;
        return  (
            <div className={"webcam"}>
                <Webcam
                    audio={false}
                    height={400}
                    width={500}
                    ref={this.webcamRef}
                    screenshotFormat="image/jpeg"

                />
                <div className={'emotionEmoji'}>{currentEmotion}</div>
                <br/>
                <div>
                    {capturedImages.map((base64Image, idx) =>
                        <span><img key={idx} alt=":)" style={{width: 100, height: 80}} src={base64Image}/></span>
                    )}
                </div>
            </div>
        );
    }
}

export default WebcamCapture;